.app-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: center;
  height: 100vh;
  text-align: center;
  background-color: #012265;
  overflow: hidden;
  padding: 10px;
}

.input-row {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 10px;
}

.input-container {
  margin: 5px;
  padding: 5px;
  border: 1px solid #ddd;
  border-radius: 5px;
  background-color: #d4af37;
  width: 30%;
  text-align: center;
  box-sizing: border-box;
}

.input-box {
  padding: 5px;
  margin-bottom: 5px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 90%;
  text-align: center;
  background-color: white;
  color: white;
}

.input-box::placeholder {
  color: white;
  opacity: 0.7;
}

.top-input {
  background-color: #0A3D91;
}

.bottom-input {
  background-color: #B22222;
}

.team-data-container {
  text-align: center;
  background-color: #d4af37;
  border-radius: 5px;
  padding: 5px;
  margin-top: 5px;
}

.section-header {
  font-weight: bold;
  margin-top: 0; /* Reduced margin to move the section higher */
  margin-bottom: 5px;
}

.grid-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2px;
}

.grid-item {
  border: 1px solid #ddd;
  padding: 2px;
  background-color: #f2f2f2;
}
